<template>
  <el-dialog
    class="grant-authorization"
    title="授权"
    :close-on-click-modal="false"
    :visible.sync="visible">
    <el-container>
      <el-aside width="30%">
        <el-scrollbar style='height: 400px;'>
          <org-tree ref="tree" :treeData="treeData" :expandAll="expandAll" @node-click="orgTreeClick"></org-tree>
        </el-scrollbar>
      </el-aside>
      <el-main>
        <el-table
          ref='table'
          :data="dataList"
          border
          v-loading="dataListLoading"
          height="400px"
          style="width: 100%;"
          @selection-change="handleSelectionChange">
          <el-table-column type="selection" width="40"></el-table-column>
          <el-table-column label="序号" type="index" width="50" align="center"></el-table-column>
          <el-table-column prop="roleCode" width="100" label="角色代码"></el-table-column>
          <el-table-column prop="roleName" label="角色名称"></el-table-column>
          <el-table-column prop="roleType" width="100" label="角色类型">
            <template slot-scope="scope">
              <div v-if="parseInt(scope.row.roleType)===1">系统管理员</div>
              <div v-if="parseInt(scope.row.roleType)===2">安全保密员</div>
              <div v-if="parseInt(scope.row.roleType)===3">安全审计员</div>
              <div v-if="parseInt(scope.row.roleType)===4">业务管理员</div>
            </template>
          </el-table-column>
        </el-table>
        <el-button type="primary" @click='saveHandle'>保存</el-button>
      </el-main>
    </el-container>
  </el-dialog>
</template>

<script>
  import orgTree from "@/components/baseComponents/orgTree.vue";
  export default{
    components: {
      orgTree
    },
    data(){
      return{
        visible:false,
        treeData:[],
        expandAll:true,
        sysId:'',
        userId:'',
        dataList:[],
        dataListLoading:false,
        multipleSelection:[]
      }
    },
    methods:{
      init(id){
        this.visible = true;
        this.userId=id;
        this.getDataTree()
      },
      getDataTree(){//获取区域节点
        this.$http({
          url: this.$http.adornUrl(this.API.getRoleMenuTree),
          method: 'post',
          data: this.$http.adornData({
            areaId:this.$store.state.user.areaId
          })
        }).then(({data}) => {
          if(data && data.code===0){
            var arr=[];
            if(data.data.length>0){
              data.data.forEach(item=>{
                arr.push({name:item.sysName,id:item.sysId})
              })
              this.treeData=[{name:'应用子系统',children:arr}];
              this.orgTreeClick(this.treeData[0].children[0])
            }
          }else{
            this.$message.error(data.msg);
            this.treeData=[];
          }
        });
      },
      //机构树点击事件
      orgTreeClick(data) {
        this.sysId=data.id;
        this.getDataList();
      },
      getDataList(){
        this.dataListLoading = true
        this.$http({
          url: this.$http.adornUrl(this.API.getRoleInfoByUserId),
          method: 'post',
          data: this.$http.adornData({
            sysId:this.sysId, //子系统ID
            areaId:this.$store.state.user.areaId,//区域ID
            userId:this.userId,
            searchCount:false,//不需要分页参数
            relateType:'0',
            relateId:this.userId
          })
        }).then(({data}) => {
          if(data && data.code===0){
            this.dataList=data.data.records;
            this.$nextTick(()=>{
              this.dataList.forEach(row=> {
                if(row.relateId!=null && row.relateId!='') this.$refs.table.toggleRowSelection(row,true);
              });
            })
          }else{
            this.$message.error(data.msg);
            this.dataList=[];
          }
          this.dataListLoading = false
        });
      },
      handleSelectionChange(e){
        this.multipleSelection=[];
        e.forEach(item=>{
          this.multipleSelection.push(item.roleId)
        })
      },
      saveHandle(){
        this.$http({
          url: this.$http.adornUrl(this.API.saveRoleuser),
          method: 'post',
          data: this.$http.adornData({
            areaId:this.$store.state.user.areaId,//区域ID
            roleId:this.multipleSelection.join(','),
            relateId:this.userId,
            relateType:'0'
          })
        }).then(({data}) => {
          if(data && data.code===0){
            this.$message.success('操作成功');
          }else{
            this.$message.error(data.msg);
          }
        });
      }
    }
  }
</script>

<style lang="scss" scoped>
  .grant-authorization{
    .el-main{text-align: center;padding: 0;}
    .el-button{margin: 10px 0 20px;}
  }
</style>



